.profile {
  display: flex;
  align-items: center;
  flex-direction: column;
  gap: 20px;
  margin: 0px 0px 200px 0px;
  padding-top: 100px;
  width: 100%;

}

.profile img {
  border-radius: 100px;
  width: 150px;
}

.profile img {
  margin-top: 50px;
}

.profile h1 {
  text-align: center;
  width: 70%;
  font-size: 40px;
  font-weight: 600;
}

.profile h1 span {
  /* background-color:rgb(29, 29, 90);
    background-clip: text ;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent; */

  text-align: center;
  width: 70%;
  font-size: 40px;
  font-weight: 600;

  /* or */
  color: gray;
  /* color: rgb(29, 29, 90); */
}

.profile p {
  width: 50%;
  text-align: center;
  font-size: 15px;
  line-height: 20px;
}

.profileAction {
  display: flex;
  align-items: center;
  gap: 25px;
  font-size: 15px;
  /* font-weight: 500; */
  /* margin-bottom: 50px; */
  cursor: pointer;
}

.profileConnect {
  padding: 10px 20px;
  /* background-color: rgb(29, 29, 90); */
  background-color: gray;
  border-radius: 100px;
  color: white;
  border: solid 2px;
}

.profileConnect:hover {
  transition: 0.5s;
  transform: scale(1.03);
}

.profileResume {
  padding: 9px 15px;
  border: 2px solid;
  border-radius: 100px;
  border-color: rgb(29, 29, 90);
}

.profileResume:hover {
  transition: 0.5s;
  transform: scale(1.03);
  border-color: gray;
}
